@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html, body {
    width: 100%;
    height: 100%;
    font-family: 'Roboto', sans-serif;
}

body {
	background: #F6FBFD;
	background-image: url(/landing-page/static/media/shutterstock_1715085037.e033ffd6.eps); 
	background-repeat: no-repeat;
	background-size: cover;
}

a { 
	text-decoration: none; 
	color: #000000;
}

h1 {
	font-weight: 400;
	text-transform: uppercase;
	text-align: center;
}

h2 {
	font-weight: 300;
	text-align: center;
}

h3 {
	text-align: center;
}

.hero {
	margin: 0;
	padding: 10%;
}

.contact {
	width: 100%;
	margin: 0;
	padding: 5% 10%;
	background: rgba(255, 255, 255, 0.7);
}

